import {
  type DropdownHeaderType,
  type DropdownItemType,
  type DropdownValueType,
} from './types';

export function isHeaderType<Key extends string, Data extends unknown = Key>(
  option: DropdownItemType<Data, Key>,
): option is DropdownHeaderType {
  return option.type === 'header';
}

export function isValueType<Key extends string, Data extends unknown = Key>(
  option: DropdownItemType<Data, Key>,
): option is DropdownValueType<Data, Key> {
  return option.type === 'value';
}
